/* Media queries to handle visibility */
@media screen and (max-width: 768px) {
    .desktop {
        display: none;
    }
    .desktopMenu {
        display: none;
    }
    .desktopMenuBtn {
        display: none;
    }
    .mobileMenuBtn {
        display: block;
    }
    
    .navMenu {
        display: flex; /* Make sure the navMenu displays as flex on mobile */
    }
    .mobMenu {
        display: flex;
    }
}